     }\n\n      &:last-of-type {\n        margin: 0 0 0 0.5%;\n      }\n    }\n  }\n\n  &[type=\"error\"] {\n    button[theme=\"primary\"].primary {\n      width: 100%;\n\n      .flex-wrapper {\n        justify-content: center;\n      }\n    }\n  }\n}\n\nnovo-notification {\n  & > section {\n    padding: 0 $spacing-xl;\n    margin: $spacing-lg 0 55px;\n\n    h1 {\n      margin: 10px auto 0;\n    }\n  }\n}\n","@import \"../common/typography/text.mixins\";\n\n.novo-card {\n  display: flex;\n  flex-flow: column;\n  background-color: var(--background-bright, $color-white);\n  box-shadow: $shadow-card;\n  border-radius: 0.4em;\n  position: relative;\n  overflow-x: hidden;\n\n  &.loading {\n    min-height: 200px;\n  }\n\n  div.card-loading-container {\n    position: absolute;\n    top: 43px;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    border-radius: 0.4em;\n    background: $white;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    z-index: z(\"default\");\n  }\n  header {\n    display: flex;\n    flex-flow: row nowrap;\n    align-items: center;\n    justify-content: space-between;\n    padding: 0.5em;\n    .title {\n      display: flex;\n      align-items: center;\n      min-width: 0;\n      flex: 1;\n      i.bhi-move {\n        color: $light;\n        margin-right: 0.3em;\n        cursor: pointer;\n      }\n      h3 {\n        @include novo-title-medium();\n        padding: 0;\n        white-space: nowrap;\n        overflow: hidden;\n        text-overflow: ellipsis;\n        width: 100%;\n        i {\n          font-size: 1.2em;\n          &.bhi-info {\n            color: $light;\n          }\n        }\n      }\n    }\n    .actions {\n      color: lighten($dark, 15%);\n      white-space: nowrap;\n    }\n  }\n\n  p.card-message {\n    padding: 20px 0;\n    max-width: inherit;\n    text-align: center;\n    line-height: 25px;\n    color: lighten($grey, 10%);\n    i {\n      display: block;\n      font-size: 2