ed, 5%) !important;\n  }\n\n  .agenda-open-day-events {\n    padding: 15px;\n    color: white;\n    background-color: #555;\n    box-shadow: inset 0 0 15px 0 rgba(0, 0, 0, 0.5);\n  }\n\n  .agenda-open-day-events .agenda-event {\n    position: relative;\n    top: 2px;\n  }\n\n  .agenda-event-title {\n    color: white;\n  }\n\n  .agenda-out-month {\n    .agenda-day-badge,\n    .agenda-event {\n      opacity: 0.3;\n    }\n  }\n}\n",".cal-week-view {\n  position: relative;\n\n  .cal-day-headers {\n    display: flex;\n    border: 1px solid #e1e1e1;\n  }\n\n  .cal-day-headers .cal-header {\n    flex: 1;\n    text-align: center;\n    padding: 5px;\n    background-color: $off-white;\n  }\n\n  .cal-day-headers .cal-header:not(:last-child) {\n    border-right: 1px solid #e1e1e1;\n  }\n\n  .cal-day-headers .cal-header:hover,\n  .cal-day-headers .cal-drag-over {\n    background-color: #ededed;\n  }\n\n  .cal-day-headers span {\n    font-weight: 400;\n    opacity: 0.5;\n  }\n\n  .cal-event-container {\n    position: absolute;\n\n    &:nth-child(n + 2) {\n      border-left: 1px solid $dark;\n    }\n\n    novo-agenda-week-event {\n      height: inherit;\n\n      .cal-event {\n        height: inherit;\n        font-size: 12px;\n        min-height: 30px;\n        display: flex;\n        flex-flow: column;\n        background-color: $off-white;\n\n        .cal-event-ribbon {\n          min-height: 4px;\n          width: 100%;\n        }\n        .cal-event-title {\n          padding: 0px 0px 0px 10px;\n          line-height: 26px;\n          overflow: hidden;\n          text-overflow: ellipsis;\n        }\n        .cal-event-description {\n          font-size: 10px;\n          line-height: 13px;\n          padding: 0px 0px 0px 10px;\n          overflow: hidden;\n          text-overflow: ellipsis;\n        }\n      }\n    }\n  }\n\n  .cal-draggable {\n    cursor: move;\n  }\n\n  .cal-header.cal-today {\n    background-color: #e8fde7;\n  }\n\n  .cal-header.cal-weekend span {\n    color: #8b0000;\n  }\n\n  .cal-event,\n  .cal-header {\n    text-overflow: ellipsis;\n    white-space: nowrap;\n  }\n}\n",".cal-day-view,\n.cal-week-view {\n  .cal-hour-rows {\n    width: 100%;\n    border: solid 1px #e1e1e1;\n    overflow-x: scroll;\n    position: relative;\n  }\n\n  .cal-hour:nth-child(even) {\n    background-color: $off-white;\n  }\n  .cal-hour:nth-child(odd) {\n    background-color: $white;\n  }\n\n  .cal-hour-segment {\n    height: 30px;\n  }\n\n  .cal-hour-segment::after {\n    content: \"\\00a0\";\n  }\n\n  .cal-hour:not(:last-child) .cal-hour-segment,\n  .cal-hour:last-child :not(:last-child) .cal-hour-segment {\n    border-bottom: thin dashed #e1e1e1;\n  }\n\n  .cal-time {\n    font-weight: bold;\n    padding-top: 5px;\n    width: 70px;\n    text-align: center;\n    color: $grey;\n  }\n\n  .cal-hour-segment.cal-after-hour-start {\n    .cal-time {\n      display: none;\n    }\n  }\n\n  .cal-hour-segment:hover,\n  .cal-drag-over .cal-hour-segment {\n    background-color: #ededed;\n  }\n}\n\n.cal-day-view {\n  .cal-event-container {\n    position: absolute;\n  }\n\n  novo-agenda-day-event {\n    height: inherit;\n\n    .cal-event {\n      height: inherit;\n      font-size: 12px;\n      margin-left: 2px;\n      margin-right: 2px;\n      min-height: 30px;\n      display: flex;\n      flex-flow: row nowrap;\n      background-color: $off-white;\n\n      .cal-event-ribbon {\n        width: 4px;\n        min-height: 100%;\n      }\n      .cal-event-group {\n        overflow: hidden;\n        flex: 1;\n        padding: 2px 10px;\n        display: flex;\n        flex-flow: column;\n      }\n      .cal-event-title {\n        line-height: 26px;\n      }\n      .cal-event-description {\n        font-size: 10px;\n        line-height: 13px;\n      }\n    }\n  }\n\n  .cal-draggable {\n    cursor: move;\n  }\n\n  .cal-all-day-event {\n    padding: 8px;\n    border: solid 1px;\n  }\n}\n","novo-agenda-date-change {\n  display: inline-block;\n\n  .cal-date-change {\n    border-radius: 3px;\n    border: 1px solid #e1e1e1;\n    background-color: $white;\n    position: relative;\n    padding: 10px 15px;\n    text-align: center;\n\n    > span {\n      padding: 5px;\n      color: $black;\n    }\n\n    > i {\n      cursor: pointer;\n      padding: 2px;\n      font-size: 0.9em;\n    }\n  }\n}\n","novo-loading {\n  padding: 20px;\n  display: flex;\n  flex-direction: row;\n  font-size: 13px;\n  gap: 0.3em;\n\n  &.size-small {\n    padding: 8px;\n    font-size: 8px;\n  }\n\n  &.size-large {\n    font-size: 18px;\n  }\n\n  span.dot {\n    display: block;\n    border-radius: 50%;\n    height: 1em;\n    width: 1em;\n    @include enumerate($dot-colors, \"background-color\", \"map-val\", \"\");\n    @include animationMapDelay($dot-colors, jump, 1600ms, ease-in-out, 70ms, forward, infinite, \"\", \"\");\n  }\n\n  @each $analytics, $color in $analytics-colors {\n    &.color-#{$analytics} {\n      span.dot {\n        background-color: $color;\n      }\n    }\n  }\n\n  &.white,\n  &[inverse] {\n    span.dot {\n      background-color: #fff !important;\n    }\n  }\n}\n\n@keyframes jump {\n  0% {\n    transform: translateY(0px);\n  }\n\n  30% {\n    transform: translateY(0px);\n    opacity: 1;\n  }\n\n  45% {\n    transform: translateY(-1.2em);\n    opacity: 0.5;\n  }\n\n  60% {\n    transform: translateY(0.8em);\n    opacity: 0.95;\n  }\n\n  100% {\n    transform: translateY(0px);\n    opacity: 1;\n  }\n}\n","// Function to loop through a SCSS map ($map) and set the map's key values to a\n// specific CSS attribute ($target) each time creating a new :nth-of-type rule at\n// the key's index.\n// For example see \"loading\" element. (BhLoading.scss)\n@mixin enumerate($map, $target, $prop, $val) {\n  $keys: map-keys($map);\n  @each $item in $keys {\n    $idx: index($keys, $item);\n    //@debug $idx;\n    //@debug $item;\n    @if $prop == \"map-val\" {\n      &:nth-of-type(#{$idx}) {\n        #{$target}: map-get($map, $item);\n      }\n    }\n\n    @if