in-width: 100px;\n    width: 100%;\n  }\n}\n\n.tabbed-group-picker-search {\n  width: 100%;\n  padding: 0.5em;\n  border-bottom: 0.1em solid #ccc;\n  position: relative;\n  input {\n    font-size: 1.2em;\n    background: transparent;\n    border: none;\n    border-radius: 0;\n    outline: none;\n    height: 2rem;\n    width: 95%;\n    margin: 0 0 0 1em;\n    padding: 0;\n    box-shadow: none;\n    box-sizing: content-box;\n    transition: all 300ms;\n    color: $dark;\n    &::-webkit-input-placeholder {\n      color: $grey;\n    }\n  }\n  i.bhi-search,\n  i.bhi-times {\n    position: absolute;\n    bottom: 1em;\n    right: 0.5em;\n    color: $grey;\n    font-size: 1em;\n    margin-right: 1em;\n  }\n  i.bhi-times {\n    cursor: pointer;\n    font-size: 1em;\n  }\n}\n\n.tabbed-group-picker-column-container {\n  display: flex;\n  flex-direction: row;\n\n  .tabbed-group-picker-column {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n\n    &.left {\n      border-right: 1px solid $light;\n      justify-content: space-between;\n\n      .clear-all-button {\n        margin-bottom: 0.5em;\n      }\n\n      novo-nav {\n        overflow: auto;\n\n        > novo-tab {\n          min-height: 3em;\n\n          > .novo-tab-link {\n            max-width: 100%;\n            height: 100%;\n            display: flex;\n            justify-content: space-between;\n            align-items: center;\n          }\n          &.active {\n            .novo-tab-link {\n              font-weight: 400;\n            }\n            &:hover {\n              .novo-tab-link {\n                font-weight: 400;\n                color: $positive;\n              }\n            }\n          }\n          &:hover {\n            .novo-tab-link {\n              font-weight: 400;\n              color: $black;\n              opacity: 1;\n            }\n          }\n        }\n      }\n    }\n\n    &.right {\n      display: block;\n      width: 23em;\n      overflow: hidden;\n\n      .