/ --------------------------------------------------\n@import \"~novo-design-tokens/scss\";\n\n@import \"./styles/variables\";\n@import \"./styles/reset\";\n@import \"./styles/base\";\n@import \"./styles/global/functions\";\n@import \"./styles/global/include-media\";\n@import \"./styles/global/flex-grid\";\n\n// --------------------------------------------------\n// Content\n// --------------------------------------------------\n@import \"./styles/typography\";\n@import \"./styles/iconography\";\n@import \"./elements/common/typography/text.mixins\";\n\n// CDK\n@import \"./styles/cdk\";\n@include cdk-overlay();\n\n// --------------------------------------------------\n// Elements\n// --------------------------------------------------\n@import \"./elements/data-table/data-table.component\";\n@import \"./elements/tabs/Tabs\";\n@import \"./elements/ace-editor/AceEditor\";\n@import \"./elements/radio/Radio\";\n@import \"./elements/toast/Toast\";\n@import \"./elements/modal/modal.component\";\n@import \"./elements/card/Card\";\n@import \"./elements/agenda/month/AgendaMonthView\";\n@import \"./elements/agenda/week/AgendaWeekView\";\n@import \"./elements/agenda/day/AgendaDayView\";\n@import \"./elements/agenda/common/AgendaDateChange\";\n@import \"./elements/loading/Loading\";\n@import \"./elements/loading/NovoSpinner\";\n@import \"./elements/select/Select\";\n@import \"./elements/picker/Picker\";\n@import \"./elements/picker/extras/grouped-multi-picker-results/GroupedMultiPickerResults\";\n@import \"./elements/picker/extras/mixed-multi-picker-results/MixedMultiPickerResults\";\n@import \"./elements/picker/extras/skills-picker-results/SkillsSpecialtyPickerResults\";\n@import \"./elements/chips/Chips\";\n@import \"./elements/dropdown/Dropdown\";\n@import \"./elements/switch/Switch\";\n@import \"./elements/date-picker/DatePicker\";\n@import \"./elements/time-picker/TimePicker\";\n@import \"./elements/date-time-picker/DateTimePicker\";\n@import \"./elements/header/Header\";\n@import \"./elements/list/List\";\n@import \"./elements/table/Table\";\n@import \"./elements/table/extras/pagination/Pagination\";\n@import \"./elements/dragula/Dragula\";\n@import \"./elements/form/Form\";\n@import \"./elements/quick-note/QuickNote\";\n@import \"./elements/tiles/Tiles\";\n@import \"./elements/tooltip/Tooltip\";\n@import \"./elements/slider/Slider\";\n@import \"./elements/tip-well/TipWell\";\n@import \"./elements/ckeditor/CKEditor\";\n@import \"./elements/category-dropdown/CategoryDropdown\";\n@import \"./elements/multi-picker/MultiPicker\";\n@import \"./elements/popover/PopOver\";\n@import \"./elements/table/extras/dropdown-cell/DropdownCell\";\n@import \"./elements/search/SearchBox\";\n@import \"./elements/places/places.component\";\n@import \"./elements/value/Values\";\n@import \"./elements/icon/Icon\";\n// @import \"./elements/expansion/expansion-panel\";\n// @import \"./elements/expansion/expansion-panel-header\";\n@import \"./elements/stepper/stepper.component\";\n@import \"./elements/stepper/step-header.component\";\n@import \"./elements/simple-table/table\";\n@import \"./elements/tabbed-group-picker/TabbedGroupPicker\";\n\n.bgc-main {\n  background-color: var(--background-main, $color-bright);\n}\n.bgc-alt {\n  background-color: var(--background-bright, $color-white);\n}\n.bgc-body {\n  background-color: var(--background-body, $color-silver);\n}\n.txc-main {\n  color: var(--text-main, $color-dark);\n}\n.txc-bright {\n  color: var(--text-bright, $color-black);\n}\n.txc-muted {\n  color: var(--text-muted, $color-slate);\n}\n\n@include theme-colors() using ($color, $name) {\n  .bgc-#{$name} {\n    background: $color;\n    // color: getContrastColor($name); // this overwrites deeper than we want\n  }\n  .bgc-#{$name}-striped {\n    & > *:nth-child(odd) {\n      background: $color;\n    }\n  }\n  .txc-#{$name} {\n    color: $color;\n  }\n}\n\n@each $key, $value in $spacing-rules {\n  .padding-#{$key} {\n    padding: $value;\n  }\n  .px-#{$key} {\n    padding-left: $value;\n    padding-right: $value;\n  }\n  .py-#{$key} {\n    padding-top: $value;\n    padding-bottom: $value;\n  }\n  .margin-#{$key} {\n    margin: $value;\n  }\n  .ml-#{$key},\n  .mx-#{$key} {\n    margin-left: $value;\n  }\n  .mr-#{$key},\n  .mx-#{$key} {\n    margin-right: $value;\n  }\n  .mt-#{$key},\n  .my-#{$key} {\n    margin-top: $value;\n  }\n  .mb-#{$key},\n  .my-#{$key} {\n    margin-bottom: $value;\n  }\n}\n\n.modal-overlay-backdrop,\n.aside-overlay-backdrop {\n  background-color: rgba(0, 0, 0, 0.8);\n}\n\n.aside-overlay-panel {\n  position: relative;\n  // z-index: 1001;\n}\n\n@keyframes iconEnter {\n  0% {\n    opacity: 0;\n    transform: scale(0.8);\n  }\n  90% {\n    opacity: 1;\n    transform: scale(1.1);\n  }\n  100% {\n    opacity: 1;\n    transform: scale(1);\n  }\n}\n","// App Container Sizing Reset\nhtml {\n  height: 100%;\n  width: 100%;\n  body {\n    height: 100%;\n    width: 100%;\n    demo-app {\n      height: 100%;\n      width: 100%;\n    }\n  }\n}\n\n*,\n*:after,\n*:before {\n  box-sizing: border-box;\n}\n","// App general body styles\nhtml {\n  margin: 0;\n  padding: 0;\n  scroll-behavior: smooth;\n\n  body {\n    margin: 0;\n    padding: 0;\n    position: relative;\n    overflow: auto;\n    novo-demo-app {\n      height: 100%;\n      width: 100%;\n      box-sizing: border-box;\n      display: block;\n    }\n  }\n\n  .markdown-page > h2 {\n    margin-top: 5rem;\n    margin-bottom: 2rem;\n    &:not(:first-child) {\n      border-top: 2px solid #ccc;\n    }\n  }\n}\n\ncode-snippet,\nmulti-code-snippet {\n  margin-bottom: 20px;\n}\n","/* Contains all Design Page /Component Demo Page SCSS imports\nstarting at line 82. >>\n=============================================================\n*/\n\n// Main content container\n.main-content {\n  position: relative;\n  min-height: 300px;\n  .container {\n    padding: 2rem;\n    margin: 0 auto;\n    max-width: 840px;\n    h1 {\n      small {\n        font-size: 14px;\n        vertical-align: middle;\n      }\n    }\n  } // Contains component demos\n  .example {\n    margin-top: 10px;\n    border: 1px solid var(--border, #e9eaea);\n    box-sizing: border-box;\n  } // Contains code snippet\n  .example-container {\n    padding: 20px 30px;\n    margin: 0 auto;\n  } // Contains component demos\n  pre {\n    margin: 0 0 35px;\n    box-sizing: border-box;\n    font-size: 0.9em;\n    background: var(--background, #f8f8f8);\n    border: 1px solid var(--border, #e9eaea);\n    color: #4c545a;\n    code {\n      border: none;\n      width: 100%;\n      display: block;\n      box-sizing: border-box;\n      border-bottom-left-radius: 3px;\n      border-bottom-right-radius: 3px;\n      line-height: 1.5em;\n      padding: 15px 25px;\n      &.hljs {\n        background: var(--background) !important;\n      }\n    }\n  } // DESIGN STYLES\n  header {\n    &.design {\n      display: flex;\n      flex-direction: row;\n      justify-content: space-between;\n      > * {\n        width: 50%;\n      }\n\n      img {\n        max-height: 250px;\n        margin-top: auto;\n        margin-bottom: auto;\n      }\n    }\n  }\n  section {\n    &.design {\n      background: $white;\n    }\n    > aside {\n      background: $background;\n      padding: 10px 20px;\n      border-radius: 3px;\n      display: inline-block;\n      border: 2px solid darken($background, 2%);\n      h6 {\n        color: $mint;\n        margin-bottom: 5px;\n      }\n      &.design-principle {\n        h6 {\n          border-bottom: 1px solid darken($background, 5%);\n          padding-bottom: 8px;\n          span {\n            font-weight: 300;\n          }\n        }\n        .hint {\n          display: block;\n          color: darken($background, 50%);\n          margin: 5px 0;\n          padding: 10px;\n          font-size: 0.9em;\n          border-radius: 3px;\n          background: darken($background, 5%);\n          border: 1px solid darken($background, 10%);\n          i {\n            margin-right: 10px;\n          }\n          > span {\n            display: block;\n          }\n          .link {\n            display: block;\n            margin-top: 10px;\n            font-weight: 400;\n            strong {\n              font-weight: 600;\n            }\n          }\n        }\n      }\n    }\n  }\n  .accepted,\n  .not-accepted {\n    display: block;\n    margin-top: 15px;\n  }\n  .not-accepted {\n    color: $negative;\n  }\n  .accepted {\n    color: $success;\n  } // Custom page element initial display reset\n  color,\n  home,\n  iconography,\n  layout,\n  typography {\n    display: block;\n    & > header {\n      background: $off-white;\n    }\n    & > section.design.container {\n      article {\n        margin: 80px 0;\n      }\n    }\n  } // DEMO STYLES\n  // Custom page element initial display reset\n  buttons-demo,\n  from-demo,\n  modal-demo,\n  switch-demo,\n  tooltip-demo {\n    display: block;\n  }\n  svg {\n    max-width: 90px;\n    display: inline-block;\n    margin: 0 auto;\n    .st0 {\n      fill: #3d464d;\n    }\n  }\n}\n\nmulti-code-snippet {\n  background: #f8f8f8;\n  border: 1px solid #e9eaea;\n  display: block;\n  label {\n    font-size: 0.8em;\n    font-weight: 500;\n    padding: 5px 10px;\n    text-transform: uppercase;\n  }\n  pre {\n    margin: 0 !important;\n    font-size: 0.8em;\n    color: #4c545a;\n    border: none !important;\n    code {\n      border: none;\n      width: 100%;\n      display: block;\n      line-height: 1.5em;\n      padding: 15px 25px;\n      &.hljs {\n        background: #f9f9f9 !important;\n      }\n    }\n  }\n}\n","// App side menu styles\n.main-nav {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 300px;\n  height: 100vh;\n  color: $white;\n  background-color: #1b2127;\n  overflow-y: scroll;\n  z-index: 20;\n\n  .fork-me {\n    width: 80%;\n    margin: 20px auto 10px;\n    padding: 10px;\n    border: 1px solid rgba(#fff, 0.15);\n    border-radius: 3px;\n    text-transform: uppercase;\n    letter-s